import React, { Component } from "react";

class Poem extends Component {
  render() {
    return <div className="Poem">
      <div className="PoemTitle"> {this.props.content.Title} </div>
      <div className="PoemBody" dangerouslySetInnerHTML={{__html: this.props.content.Body}}></div>
    </div>;
  }
}

export default Poem;
