import React, { Component } from "react";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faChevronLeft, faChevronRight } from "@fortawesome/free-solid-svg-icons";
import { Link } from 'react-router-dom';

class Calander extends Component {
  constructor(props) {
    super(props);
    this.state = {
      calender: this.createCalendar(),
      currentMonth:  new Date().getMonth(),
      monthString: this.parseMonth(new Date().getMonth()),
    };
  }

  parseMonth(monthNum){
    var dates=["JAN", "FEB", "MAR", "APR", "MAY", "JUN", "JUL", "AUG", "SEP", "OCT", "NOV", "DEC"];
    var dateLetters = dates[monthNum].split('');
    return <div id="month">{dateLetters[0]} &emsp;{dateLetters[1]}&emsp;{dateLetters[2]}</div>  ;
  }

  createCalendar(month=null) {
    var currentMonth = month!==null? month : new Date().getMonth();
    var date = new Date(new Date().getFullYear(),currentMonth , 1)
    var dates = [];

    while(date.getMonth() === currentMonth){
      var dayDate = [];
      dayDate.push(date.getUTCDay());
      dayDate.push(date.getUTCDate());
      dates.push(dayDate);
      date.setDate(date.getDate()+1);
    }

    var rows = [];
    var j =0;

    while(j<dates.length-1){      
      var columns = [];
      for (var i = 0; i<7; i++){         
        
        if(j<dates.length && dates[j][1]===new Date().getDate() && currentMonth === new Date().getMonth()){  
          var thisDate = new Date(new Date().getFullYear(),currentMonth, dates[j][1]);
          var link = `/journal/${thisDate.getDate()}${thisDate.getMonth()}${thisDate.getFullYear()}`;
          columns.push(
            <td key={i.toString()+j.toString()} id ="today"><Link to={link} className="clickable">{dates[j][1]}</Link></td>
          );
          j++;
        }
        else if(j<dates.length && dates[j][0]===i){
          var thisDate = new Date(new Date().getFullYear(),currentMonth, dates[j][1]);
          var link = `/journal/${thisDate.getDate()}${thisDate.getMonth()}${thisDate.getFullYear()}`;
          columns.push(
            <td key={i.toString()+j.toString()} ><Link to={link} className="clickable">{dates[j][1]}</Link></td>
          );
          j++;
        }
        else{
          columns.push(
            <td key={i.toString()+j.toString()}> {""}</td>
          );
        }
      }
      rows.push(
        <tr key={j.toString()}>
          {columns}
        </tr>
      );
    }
    return rows;
  }

  changeCalender(i){
    var newMonth = this.state.currentMonth + i;
    newMonth = newMonth > 11 ? 0 : newMonth;
    newMonth = newMonth < 0 ? 11 : newMonth;
    this.setState({
      currentMonth: newMonth,      
      calender:this.createCalendar(newMonth),
      monthString: this.parseMonth(newMonth)
    });      
  }

  render() {
    return (<div className="Calendar">
      {this.state.monthString}
      <FontAwesomeIcon icon={faChevronLeft} className="clickable" onClick={()=> this.changeCalender(-1)}/>
      <table>
        <tbody>
        <tr>        
          <th>S</th>
          <th>M</th>
          <th>T</th>
          <th>W</th>
          <th>T</th>
          <th>F</th>
          <th>S</th>
        </tr>
        {this.state.calender}
        </tbody>
      </table> 
      <FontAwesomeIcon icon={faChevronRight} className="clickable" onClick={()=> this.changeCalender(1)}/>
    </div>);
  }
}

export default Calander;
