import React from 'react';
import ReactDOM from 'react-dom';
import Navbar from './Components/navbar';
import Calander from './Components/Calander';
import JournalEntry from './Components/JournalEntry';
import Poem from './Components/Poem';
import { BrowserRouter as Router, Route } from 'react-router-dom';
import './index.css';

class App extends React.Component{
    
    
    render(){
        return ( 
            
        <Router basename={'Journal'}> 
            <div className = "App">       
                <Navbar/>               
                <Route exact path ="/" component={this.Home}/>
                <Route exact path ="/journal" component={this.Journal} />                             
                <Route exact path ="/poems" component={this.Poems} />                       
                <Route path ="/journal/:entryId" component={this.Entry} />      
            </div>
        </Router>
        );
    }

    Home(){
        return (<div className="Home">  
            <div id="HomeCentre">
                <img src={require("./img/candle.png")} width="400vw" alt="landing"></img>                
                <p style={{margin:0}}>Journal De Maria</p>     
                <div> Lorem ipsum dolor sit amet, consectetur adipiscing elit.</div>
                <div>
                    <ul>
                        <li>Featured Peom</li>
                        <li>Todays Entry</li>
                        <li><a href="https://commaful.com/play/hurley/" className="clickable">Commaful</a></li>
                    </ul>
                </div>
            </div>
        </div>);
    }

    Journal(){
        return(<div>
            <Calander/>
        </div>)
    }

    Entry({match}){
        return(<div>
            <JournalEntry entryId={match.params.entryId} getJson={App.getJson}/>
        </div>)
    }

    Poems(){
        return(<div id="PoemContainer">
            {App.getPoems()}
        </div>)
    }

    static getPoems(){
       var poems = this.getJson("Poems");
       console.log(poems);
       var poemsHtml = [];
       poems.forEach(poem => {
            poemsHtml.push(
                <Poem content = {poem} />
            );
       });
       return poemsHtml;
    }

    static getJson(link){
        //Http Request HERE
        //=============================================================
        var url = `https://journal-fb8bd.firebaseio.com/${link}.json`;
        var xmlHttp = new XMLHttpRequest();
        xmlHttp.open( "GET", url, false ); 
        xmlHttp.send( null );
        //--------------------------------------------------------------
        return  JSON.parse (xmlHttp.responseText);
      }
}

ReactDOM.render(
    <App  />,
    document.getElementById('root')
);