import React, { Component } from "react";

class JournalEntry extends Component {
  render() {
    var entry = this.props.getJson(`Entries/${this.props.entryId}`);
    if(entry === null) return <div className="JournalEntry"> Oops no entry has been entered as yet </div>
    
    return (<div className="JournalEntry">
      <div className="EntryContent"> 
        <img src={require(`../../img/${entry.Image}`)} width="400vw" alt="landing"></img> 
        <div id="EntryTitle"> {entry.Title}</div>
        <div id="EntryBody" dangerouslySetInnerHTML={{__html: entry.Body}}/> 
      </div>
    </div>);
  }

  
}

export default JournalEntry;
