import React, { Component } from "react";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCircle, faNewspaper, faStar } from "@fortawesome/free-regular-svg-icons";
import { faFeatherAlt, faHome } from "@fortawesome/free-solid-svg-icons";
import Sidebar from "react-sidebar";
import {Link } from 'react-router-dom';

class Navbar extends Component {
  constructor(props) {
    super(props);
    this.state = {
      sidebarOpen: false
    };
    class refPair{
      constructor(){
        this.Button = React.createRef();
        this.ToolTip = React.createRef();
      }
    };
    this.myRefs = {
      journal : new refPair(),
      home : new refPair(),
      poems : new refPair(),
      featured : new refPair()
    }
    this.testRef = React.createRef();
    this.onSetSidebarOpen = this.onSetSidebarOpen.bind(this);
  }

  onSetSidebarOpen(open) {
    document.getElementById("mySidebarRoot").style.zIndex = open? 1: -1;         
    this.setState({ sidebarOpen: open });
    // console.log(this.testRef.current.children[0].children[0].getBoundingClientRect().top);
  }

  displayTip(id){
    var tip = this.myRefs[id].ToolTip.current;
    tip.style.visibility = "visible";
    tip.style.left = "90px";
    tip.style.top = `${this.setTipTop(id)}px`;    
  }

  removeTip(id){    
    this.myRefs[id].ToolTip.current.style.visibility = "hidden";
  }

  setTipTop(id){
    return this.myRefs[id].Button.current.children[0].children[0].getBoundingClientRect().top - 3;
  }

  render() {
    return (
      
      <div className="Navbar" >    
        <Sidebar rootId = "mySidebarRoot" sidebarClassName="mySidebar"
        children = {<h1>HI</h1>}
          sidebar={       
            <div>              
              <ul>
                <li><div className="tooltip" ref={this.myRefs.home.Button} onMouseEnter={()=> this.displayTip("home")} onMouseLeave ={()=>this.removeTip("home")}>
                  <Link to="/" className="navLink">
                    <FontAwesomeIcon icon={faHome} className="navIcon"/>
                  </Link>
                </div></li>
                <li>
                  <div className="tooltip" ref={this.myRefs.journal.Button} onMouseEnter={()=> this.displayTip("journal")} onMouseLeave ={()=>this.removeTip("journal")} >
                    <Link to="/journal" className="navLink" >
                      <FontAwesomeIcon icon={faNewspaper} className="navIcon"  />
                    </Link>
                  </div>
                </li>
                <li>
                  <div className="tooltip" ref={this.myRefs.poems.Button} onMouseEnter={()=> this.displayTip("poems")} onMouseLeave ={()=>this.removeTip("poems")}>
                    <Link to="/poems" className="navLink">
                      <FontAwesomeIcon icon={faFeatherAlt}className="navIcon"/>
                    </Link>
                  </div>
                </li>
                <li>
                  <div className="tooltip" ref={this.myRefs.featured.Button} onMouseEnter={()=> this.displayTip("featured")} onMouseLeave ={()=>this.removeTip("featured")}>
                    <div>
                      <FontAwesomeIcon icon={faStar}className="navIcon"/>
                    </div>
                  </div>
                </li>
              </ul>
              <img src={require(`../../img/face.png`)}  alt="sideDeco"></img> 
            </div>
            
          }
          open={this.state.sidebarOpen}
          onSetOpen={this.onSetSidebarOpen}
          styles={{ sidebar: { 
            background: "rgb(255, 250, 244)",
            padding: 10,
            position: 'fixed',
            overflowY: 'hidden',
          }}}
        >
        
        </Sidebar>
        <div id="top">
          <h1 id="menuBut"><FontAwesomeIcon icon={faCircle} onClick={() => this.onSetSidebarOpen(!this.state.sidebarOpen)}/></h1>
          <h1 id="year">{(new Date()).getFullYear()}</h1>
        </div>
      
        <span className="tooltiptext" ref={this.myRefs.home.ToolTip} >Home</span>
        <span className="tooltiptext" ref={this.myRefs.journal.ToolTip} >Journal</span>
        <span className="tooltiptext" ref={this.myRefs.poems.ToolTip} >Poems</span>
        <span className="tooltiptext" ref={this.myRefs.featured.ToolTip} >Daily Feature</span>
    
    </div>
    );
  }
}

export default Navbar;
